import router from './router'
import store from './store'
// import { Message } from 'element-ui'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css'// progress bar style
import { getToken } from 'common/utils' // getToken from cookie
import { axiosClient } from 'common/axiosClient'

NProgress.configure({ showSpinner: false })// NProgress Configuration

// permission judge function

router.beforeEach((to, from, next) => {
  NProgress.start() // start progress bar 
  if (getToken()) { // token
    /* has token*/
    // headertoken
    axiosClient.defaults.headers.common['token'] = getToken();
    // axiosClient.defaults.headers.common['token'] = '111'
    axiosClient.defaults.headers.common['userId'] = store.state.auth.userInformation.id;
    if (to.path === '/login') {
      next({ path: '/' })
      NProgress.done() // 
    } 
    else {
      // if (localStorage.getItem('userInformation')) { // 
        // store.dispatch('GetUserInfo').then(res => { // user_info
          // const roles = res.data.roles // note: roles must be a array! such as: ['editor','develop']
          if (store.state.auth.permissionList.length === 0) { // ()
            store.dispatch('getUserInfo').then( () => { // 
              store.commit('setLogin', JSON.parse(localStorage.getItem('userInformation'))); // 
              store.dispatch('generateRoutes').then(() => { // 
                // console.log('', store.getters.addRouters)
                router.addRoutes(store.getters.addRouters) // 
                // next();
                next({ ...to, replace: true }) // , elsenext()
              })
            }).catch( () => {
              store.dispatch('Fedout').then( () => {
                next({path: '/'})
              })
            })
          } 
          else { // , next()
            next();
          }
        // })
      // } 
    }
  } else { 
    /** 
     * token
     * : next() to.path, , next(); , 
     */
      if (to.path == '/login') {
        next();
      } else {
        next('/login')
      }
    // next('/login')
    // store.dispatch('Fedout').then( () => {
    //   NProgress.done();
    //   if (to.path == '/login') {
    //     next();
    //   } else {
    //     next('/login')
    //   }
    // })
  }
})

router.afterEach(() => {
  NProgress.done() // finish progress bar
})



